import * as THREE from './node_modules/three/build/three.module.js';
import { OrbitControls } from './node_modules/three/examples/jsm/controls/OrbitControls.js';

import { d1 } from './dane.js';


let scene, camera, renderer, controls;
let layerSpacing = 2;

function getMaxVal(data) {
    let max = -Infinity;
    for (let i = 0; i < data.length; i++) {
        for (let j = 0; j < data[i].length; j++) {
            if (data[i][j] > max) {
                max = data[i][j];
            }
        }
    }
    return max;
}

function clearScene() {
    for (let i = scene.children.length - 1; i >= 0; i--) {
        const object = scene.children[i];
        if (object instanceof THREE.Mesh) {
            object.geometry.dispose();
            object.material.dispose();
            scene.remove(object);
        }
    }
}

document.getElementById('minusSpacing').addEventListener('click', function() {
    clearScene();
    layerSpacing +=10;
    addDataToScene(d1);
});

document.getElementById('plusSpacing').addEventListener('click', function() {
    clearScene();
    layerSpacing -=10;
    addDataToScene(d1);
});



function addDataToScene(data) {
    const maxValue = getMaxVal(data);
    const thresh = maxValue / 10;
    

    const bigBois = 0.3;
    const spacing = 1.5;

    // for (let y = 0; y < data.length; y++) {
    //     for (let x = 0; x < data[y].length; x++) {

    //         const layer = Math.floor(data[y][x] / thresh);

    //         if (data[y][x] < (layer + 1) * thresh) {
    //             const sphereGeometry = new THREE.SphereGeometry(bigBois, 8, 8);
    //             const sphereMaterial = new THREE.MeshBasicMaterial({ color: 0x00ff00 });
    //             const sphere = new THREE.Mesh(sphereGeometry, sphereMaterial);

    //             sphere.position.set(x * spacing, y * spacing, layer * layerSpacing);
    //             scene.add(sphere);
    //         }
    //     }
    // }

            const sphereGeometry = new THREE.SphereGeometry(bigBois, 8, 8);
        const sphereMaterial = new THREE.MeshBasicMaterial({ color: 0x00ff00 });
        const instances = data.length * data[0].length * 10; // zamy, e maksymalnie bdziesz mia tyle instancji
        const instancedSphere = new THREE.InstancedMesh(sphereGeometry, sphereMaterial, instances);

        let instanceCounter = 0;
        for (let y = 0; y < data.length; y++) {
            for (let x = 0; x < data[y].length; x++) {
                const layer = Math.floor(data[y][x] / thresh);
                for (let currentLayer = 0; currentLayer <= layer; currentLayer++) {
                    const matrix = new THREE.Matrix4();
                    matrix.setPosition(x * spacing, y * spacing, currentLayer * layerSpacing);
                    instancedSphere.setMatrixAt(instanceCounter++, matrix);
                }
            }
        }

        scene.add(instancedSphere);

}




function init() {
    scene = new THREE.Scene();
    camera = new THREE.PerspectiveCamera(75, window.innerWidth / window.innerHeight, 0.1, 1000);
    camera.position.set(50, -200, -300);  //hate it 
    camera.lookAt(scene.position);

    renderer = new THREE.WebGLRenderer({ antialias: true });
    renderer.setSize(window.innerWidth, window.innerHeight);
    document.body.appendChild(renderer.domElement);

    controls = new OrbitControls(camera, renderer.domElement); 

    addDataToScene(d1, 2);
    animate();
}


function animate() {
    requestAnimationFrame(animate);
    controls.update();
    renderer.render(scene, camera);
}

init();
